<template>
	<div class="container">
		<cube-upload
		  ref="upload"
		  v-model="files"
		  :max="max"
		  :process-file="processFile"
		  @files-added="addedHandler"
		  @file-submitted="fileSubmitted"
		  @file-removed="deleHandler"
		  @file-error="errHandler">
		  <div :class="[isAlone?'deleStyle':'']" class="clear-fix">
		    <cube-upload-file v-for="(file, i) in files" :file="file" :key="i"></cube-upload-file>
		    <cube-upload-btn  v-show="isAlone||files.length<max" :multiple="multiple">
		      <div>
		        <i class="fa fa-camera" aria-hidden="true"></i>
		        <span>{{intro}}</span>
		      </div>
		    </cube-upload-btn>
		  </div>
		</cube-upload>
	</div>
</template>
<script>
import compress from "@/assets/js/image";
import {Style,Upload} from 'cube-ui';
import {mapState} from 'vuex';
export default{
	props:{
		field:{
			type:String,
			default:""
		},
		intro:{
			type:String,
			default:""
		},
		multiple:{
			type:Boolean,
			default:true
		},
		isAlone:{
			type:Boolean,
			default:false
		},
		max:{
			type:Number,
			default:1
		},
		subclass:{
			type:String,
			default:"1"
		},
		orderNo:{
			type:String,
			default:""
		},
		largeclass:{
		 	type:String,
		 	default:""
		}
	},
	name:"imgUpload",
	data(){
		return {
			files:[],
			claimNo:this.$route.query.claimNo,
			organNo:this.$route.query.organNo,
			orderNoNum:this.orderNo
		}
	},
	methods:{
		addedHandler(data) {
			if(this.isAlone){
				const file = this.files[0]
      			file && this.$refs.upload.removeFile(file)
			}
	    	this.$emit("addNum",data.length,this.field,this.max)
	    },
	    deleHandler(){
	    	this.$emit("deleNum",this.field)
	    },
	    errHandler(file) {
	      // const msg = file.response.message
	      this.$createToast({
	        type: 'warn',
	        txt: '上传失败',
	        time: 1000
	      }).show()
	    },
		processFile(file, next) {
	     	compress(file, {
		        compress: {
		          quality: 0.8,
		          width:1080
		        }
	      	},next)
	    },
	    fileSubmitted(file){
	    	delete file.file;
	    	delete file.url;
	    	delete file.name;
	    	delete file.size;
	    	delete file.progress;
	    	file.type=this.subclass;
	    	file.claimNo=this.claimNo;
	    	file.status=this.largeclass;
	    	if(this.orderNo){
	    		file.pictureorder=this.orderNoNum;
	    		file.name=this.organNo+"_"+this.claimNo+"_"+this.orderNoNum+++"_"+this.largeclass+"_"+2;
	    	}
	    }
	},
	components:{
		CubeUpload:Upload,
		CubeUploadBtn:Upload.Btn,
		CubeUploadFile:Upload.File
	}
}
</script>
<style lang="stylus" scoped>
.cube-upload{
	.deleStyle{
		.cube-upload-file{
			+ .cube-upload-btn{
				opacity: 0;
				position: absolute;
			}
		}
		>>>.cube-upload-file-def{
			.cubeic-wrong{
				display:none;
			}
		}
	}
	.clear-fix{
		.cube-upload-btn{
			text-align:center;
			display: flex;
			align-items: center;
			justify-content: center;
			div{
				display:flex;
				flex-direction: column;
				span{
					margin-top:5px;
					color:#ccc;
				}
			}
			.fa-camera{
				font-size:25px;
			}
		}
		>>>.cube-upload-file-def{
			width:100%;
			height:100%;
		}
		.cube-upload-file,.cube-upload-btn{
			height:104px;
			width:134px;
			margin:5px;
			float:left;
			border:1px solid #ccc;
		}
	}
	
}

</style>



