<template>
	<div class="container">
		<my-header title="影像上传"></my-header>
		<section>
			<div>
				<h5>
					<span>申请人身份证件</span>
					<span>已上传<strong>{{idFrontNum+idBackNum}}</strong>张</span>
				</h5>
				<div style="display:flex">
					<img-upload ref="idFrontNum" subclass="2" :largeclass="claimApply.documentNo.customerInfo" orderNo="1001" intro="身份证正面" field="idFrontNum" @addNum="addNumFun" :multiple=false  :isAlone=true></img-upload>
					<img-upload ref="idBackNum" subclass="2" :largeclass="claimApply.documentNo.customerInfo" orderNo="1002" intro="身份证反面" field="idBackNum" @addNum="addNumFun" :multiple=false :isAlone=true></img-upload>
				</div>
			</div>
			<div>
				<h5>
					<span>申请人银行账户</span>
					<span>已上传<strong>{{bankFrontNum+bankBackNum}}</strong>张</span>
				</h5>
				<div style="display:flex">
					<img-upload subclass="5" :largeclass="claimApply.documentNo.customerInfo" ref="bankFrontNum" orderNo="1003" intro="银行卡正面" field="bankFrontNum" @addNum="addNumFun" :multiple=false :isAlone=true></img-upload>
					<img-upload subclass="5" :largeclass="claimApply.documentNo.customerInfo" ref="bankBackNum" orderNo="1004" intro="银行卡反面" field="bankBackNum" @addNum="addNumFun" :multiple=false :isAlone=true></img-upload>
				</div>
			</div>
			<div v-show="!isAdult">
				<h5>
					<span>出险人身份证件</span>
					<span>已上传<strong>{{insureIdFrontNum+insureIdBackNum}}</strong>张</span>
				</h5>
				<div style="display:flex">
					<img-upload subclass="3" :largeclass="claimApply.documentNo.customerInfo" ref="insureIdFrontNum" orderNo="1005" intro="身份证正面" field="insureIdFrontNum" @addNum="addNumFun" :multiple=false :isAlone=true></img-upload>
					<img-upload subclass="3" :largeclass="claimApply.documentNo.customerInfo" ref="insureIdBackNum" orderNo="1006" intro="身份证反面" field="insureIdBackNum" @addNum="addNumFun" :multiple=false :isAlone=true></img-upload>
				</div>
			</div>
			<div v-show="!isAdult">
				<h5>
					<span>申请人与出险人关系证明</span>
					<span>已上传<strong>{{relaProvNum}}</strong>张</span>
				</h5>
				<div>
					<img-upload subclass="4" :largeclass="claimApply.documentNo.customerInfo" ref="relaProvNum" field="relaProvNum" @addNum="addNumFun" @deleNum="deleNumFun" :max=5></img-upload>
				</div>
			</div>
			<div  v-show="$route.query.claimType!=5">
				<h5>
					<span>诊断证明／出院记录</span>
					<span>已上传<strong>{{diagnosisNum}}</strong>张</span>
				</h5>
				<div>
					<img-upload subclass="9" :largeclass="claimApply.documentNo.auditData" ref="diagnosisNum" field="diagnosisNum" @addNum="addNumFun" @deleNum="deleNumFun" :max=5></img-upload>
				</div>
			</div>
			<div v-show="$route.query.claimType==3">
				<h5>
					<span>残疾相关证明</span>
					<span>已上传<strong>{{disabilityNum}}</strong>张</span>
				</h5>
				<div>
					<img-upload subclass="12" :largeclass="claimApply.documentNo.auditData" ref="disabilityNum" field="disabilityNum" @addNum="addNumFun" @deleNum="deleNumFun" :max=20></img-upload>
				</div>
			</div>
			<div v-show="$route.query.claimType==4">
				<h5>
					<span>确诊重疾的相关检查或化验报告</span>
					<span>已上传<strong>{{strichenNum}}</strong>张</span>
				</h5>
				<div>
					<img-upload subclass="10" :largeclass="claimApply.documentNo.auditData" ref="strichenNum" field="strichenNum" @addNum="addNumFun" @deleNum="deleNumFun" :max=20></img-upload>
				</div>
			</div>
			<div v-show="$route.query.claimType==5">
				<h5>
					<span>出院记录</span>
					<span>已上传<strong>{{hospitalNum}}</strong>张</span>
				</h5>
				<div>
					<img-upload subclass="6" :largeclass="claimApply.documentNo.auditData" ref="hospitalNum" field="hospitalNum" @addNum="addNumFun" @deleNum="deleNumFun" :max=5></img-upload>
				</div>
			</div>
			<div v-show="$route.query.claimType==5">
				<h5>
					<span>发票原件(若有分割单一并拍摄)</span>
					<span>已上传<strong>{{invoiceNum}}</strong>张</span>
				</h5>
				<div>
					<img-upload subclass="7" :largeclass="claimApply.documentNo.auditData" ref="invoiceNum" field="invoiceNum" @addNum="addNumFun" @deleNum="deleNumFun" :max=10></img-upload>
				</div>
			</div>
			<div v-show="$route.query.claimType==5">
				<h5>
					<span>费用清单</span>
					<span>已上传<strong>{{costListNum}}</strong>张</span>
				</h5>
				<div>
					<img-upload subclass="8" :largeclass="claimApply.documentNo.auditData" ref="costListNum" field="costListNum" @addNum="addNumFun" @deleNum="deleNumFun" :max=10></img-upload>
				</div>
			</div>
			<div v-show="$route.query.claimType==6">
				<h5>
					<span>确诊轻症的相关检查或化验报告</span>
					<span>已上传<strong>{{mildillnessNum}}</strong>张</span>
				</h5>
				<div>
					<img-upload subclass="11" :largeclass="claimApply.documentNo.auditData" ref="mildillnessNum" field="mildillnessNum" @addNum="addNumFun" @deleNum="deleNumFun" :max=20></img-upload>
				</div>
			</div>
			<div>
				<h5>
					<span>其他补充资料</span>
					<span>已上传<strong>{{otherNum}}</strong>张</span>
				</h5>
				<div>
					<img-upload subclass="13" :largeclass="claimApply.documentNo.otherInformation" ref="otherNum" field="otherNum" @addNum="addNumFun" @deleNum="deleNumFun" :max=10></img-upload>
				</div>
			</div>
			<div style="margin-bottom:0;">
				<foot-button @next="toConfirmSub"></foot-button>
			</div>
		</section>
	</div>
</template>
<script>
import MyHeader from "@/components/model/MyHeader";
import ImgUpload from "@/components/pages/claimApply/components/ImgUpload";
import FootButton from "@/components/pages/claimApply/components/FootButton";
import {QUERYUPLOADIMAGES,UPLOADIMAGES} from "@/api";
import {ISINPUTNULL} from "@/utils/publicUtils";
import {mapState} from "vuex";
export default {
  name: 'Uploading',
  data(){
  	return {
  		action:"",
  		idFrontNum:0,
  		idBackNum:0,
  		bankFrontNum:0,
  		bankBackNum:0,
  		hospitalNum:0,
  		insureIdFrontNum:0,
  		diagnosisNum:0,
  		insureIdBackNum:0,
  		invoiceNum:0,
  		mildillnessNum:0,
  		relaProvNum:0,
  		strichenNum:0,
  		costListNum:0,
  		disabilityNum:0,
  		otherNum:0,
  		file:[],
  		claimNo:this.$route.query.claimNo,
		organNo:this.$route.query.organNo
  	}
  },
  async mounted(){
   	let datas=await QUERYUPLOADIMAGES({claimNo:this.claimNo});
  	datas.data.data.map((item)=>{
  		this.echoJudge(item);
  	})
  },
  computed:{
  	...mapState(['claimApply','documentNo']),
	isAdult(){
		let age=this.$Moment.duration(this.$Moment().diff(this.$route.query.insuredBirth)).years();
		if(age>18){
			 return true;
		}else{
			return false;
		}
	}
  },
  methods:{
  	echoJudge(item){
  		switch(item.type){
  			case "2":
	  			switch(item.pictureorder){
	  				case "1001":
		  				this.$refs.idFrontNum.files.push(this.nullReg(item));
		  				this.idFrontNum++;
		  				break;
		  			case "1002":
		  				this.$refs.idBackNum.files.push(this.nullReg(item));
		  				this.idBackNum++
		  				break;
	  			}
	  			break;
  			case "3":
  				if(!this.isAdult){
  					switch(item.pictureorder){
	  				case "1005":
		  				this.$refs.insureIdFrontNum.files.push(this.nullReg(item));
		  				this.insureIdFrontNum++;
		  				break;
		  			case "1006":
		  				this.$refs.insureIdBackNum.files.push(this.nullReg(item));
		  				this.insureIdBackNum++;
		  				break;
	  				}
  				}
	  			break;
	  		case "4":
		  		if(!this.isAdult){
		  			this.$refs.relaProvNum.files.push(this.nullReg(item));
		  			this.relaProvNum++;
		  		}	
		  			break;
	  		case "5":
	  			switch(item.pictureorder){
	  				case "1003":
		  				this.$refs.bankFrontNum.files.push(this.nullReg(item));
		  				this.bankFrontNum++;
		  				break;
		  			case "1004":
		  				this.$refs.bankBackNum.files.push(this.nullReg(item));
		  				this.bankBackNum++;
		  				break;
	  			}
	  			break;
	  		case "6":
	  			if(this.$route.query.claimType==5){
	  				this.$refs.hospitalNum.files.push(this.nullReg(item));
	  				this.hospitalNum++;
	  			}
	  			break;
	  		case "7":
	  			if(this.$route.query.claimType==5){
	  				this.$refs.invoiceNum.files.push(this.nullReg(item));
	  				this.invoiceNum++;
	  			}
	  			break;
  			case "8":
  				if(this.$route.query.claimType==5){
  					this.$refs.costListNum.files.push(this.nullReg(item));
	  				this.costListNum++;
  				}
	  			break;
	  		case "9":
	  			this.$refs.diagnosisNum.files.push(this.nullReg(item));
	  			this.diagnosisNum++;
	  			break;
	  		case "10":
		  		if(this.$route.query.claimType==4){
		  			this.$refs.strichenNum.files.push(this.nullReg(item));
		  			this.strichenNum++;
		  		}
	  			break;
	  		case "11": //
	  			if(this.$route.query.claimType==6){
	  				this.$refs.mildillnessNum.files.push(this.nullReg(item));
	  				this.mildillnessNum++;
	  			}
	  			break;
	  		case "12":	//
		  		if(this.$route.query.claimType==3){
		  			this.$refs.disabilityNum.files.push(this.nullReg(item));
		  			this.disabilityNum++;
		  		}
	  			break;
	  		case "13":	//其他
	  			this.$refs.otherNum.files.push(this.nullReg(item));
	  			this.otherNum++;
	  			break;
  		}
  	},
  	nullReg(item){
  		item.base64=item.base64.replace(/\s+/g,"");
  		return item
  	},
  	toConfirmSub(){
  		if(
	  		ISINPUTNULL(this,this.idFrontNum,"申请人身份证件正面")&&ISINPUTNULL(this,this.idBackNum,"申请人身份证件反面")&&
	  		ISINPUTNULL(this,this.bankFrontNum,"银行卡正面")&&ISINPUTNULL(this,this.bankBackNum,"银行卡反面")&&
	  		(this.isAdult||ISINPUTNULL(this,insureIdFrontNum,"出险人身份证件正面")&&ISINPUTNULL(this,insureIdFrontNum,"出险人身份证件反面")&&ISINPUTNULL(this,this.relaProvNum,"申请人与出险人关系证明"))&&
	  		(this.$route.query.claimType==5||ISINPUTNULL(this,this.diagnosisNum,"诊断证明／出院记录"))&&
	  		(this.$route.query.claimType!=3||ISINPUTNULL(this,this.disabilityNum,"残疾相关证明"))&&
	  		(this.$route.query.claimType!=4||ISINPUTNULL(this,this.strichenNum,"确诊重疾的相关检查或化验报告"))&&
	  		(this.$route.query.claimType!=5||ISINPUTNULL(this,this.hospitalNum,"出院记录")&&ISINPUTNULL(this,this.invoiceNum,"发票原件")&&ISINPUTNULL(this,this.costListNum,"费用清单"))&&
	  		(this.$route.query.claimType!=6||ISINPUTNULL(this,this.mildillnessNum,"确诊轻症的相关检查或化验报告"))
	  	){
  			let data=[];
	  		this.file=data.concat(
	  			this.$refs.idFrontNum.files,
	  			this.$refs.idBackNum.files,
	  			this.$refs.bankFrontNum.files,
	  			this.$refs.bankBackNum.files,
	  			this.$refs.insureIdFrontNum.files,
	  			this.$refs.insureIdBackNum.files,
	  			this.picName([].concat(this.$refs.relaProvNum.files),this.isAdult?"1005":"1007"),
	  			this.picName([].concat(
	  				this.$refs.diagnosisNum.files,
	  				this.$refs.disabilityNum.files,
	  				this.$refs.strichenNum.files,
	  				this.$refs.hospitalNum.files,
	  				this.$refs.invoiceNum.files,
	  				this.$refs.costListNum.files,
	  				this.$refs.mildillnessNum.files
	  			)),
	  			this.picName([].concat(this.$refs.otherNum.files))
	  		);
	  		UPLOADIMAGES({data:this.file}).then((res)=>{
	  			this.$router.push({path:this.$route.path+"/confirmsub",query:{claimNo:this.$route.query.claimNo}});
	  		})
	  	}
  	},
    deleNumFun(field){
    	this[field]--;
    },
    picName(arr,order='1001'){
    	arr.forEach((item)=>{
    		if(!item.claimNo){
    			item.claimNo=this.claimNo;
    		}
			item.pictureorder=order+"";
			item.name=this.organNo+"_"+this.claimNo+"_"+order+++"_"+item.status+"_"+2;
		});
    	return arr;
    },
  	addNumFun(num,field,max){
    	this[field]+=num;
    	this[field]=this[field]>max?max:this[field];
    }
  },
  components:{
  	MyHeader,ImgUpload,FootButton
  }
}
</script>
<style lang="stylus" scoped>
	.container{
		height:100%;
		display:flex;
		flex-direction: column;
		&>section{
			flex:1;
			height:100%;
			overflow-y:auto;
			&>div{
				background:#fff;
				padding:0 15px;
				margin-bottom:15px;
				&>h5{
					height:40px;
					line-height:50px
					display:flex;
					font-size:14px;
					justify-content: space-between;
					&>span:nth-child(2){
						font-size:16px;
						color:#ccc;
						&>strong{
							color:#fd7f47;
							margin:0 5px;
						}
					}
				}
				&>div{
					padding-bottom:20px;
					&>.cube-upload{
						display:inline-block;
						margin-right:10px;
						width:136px;
						height:104px;
						line-height:104px;
						text-align:center;
						border:1px solid #ccc;
						.fa-camera{
							font-size:30px;
						}
					}
				}
			}
		}
	}
</style>



